import React, { useEffect } from 'react';
import { Outlet, useNavigate } from 'react-router-dom';

const BlankLayout = () => {
  const navigate = useNavigate();
  useEffect(() => {
    const isAuth = localStorage.getItem('');
    if (isAuth) {
      return navigate('/dashboards/tables');
    }
  }, []);

  return (
    <>
      <Outlet />
    </>
  );
};

export default BlankLayout;
